import { Autocomplete, TextField } from '@mui/material'
import { getAnimals } from '../data/dataFetcher'
import { Animal } from '../models/Animal'
import { useNavigate } from "react-router-dom";
import React, { useState } from 'react';

const SearchBar = () => {
    const [value, setValue] = useState<string | Animal | null | undefined>("");
    const navigate = useNavigate();
    const handleOnChage = (
        event: React.SyntheticEvent<Element, Event>,
        newInputValue: string | Animal | null,
        reason: string): void => {
            console.log(value)
        if (newInputValue === null || (newInputValue as Animal).id === null) {
            return;
        }
        navigate(`/animal/${(newInputValue as Animal).id}`);
        setValue("");
        console.log(value)
    };

    return (
        <Autocomplete
            disablePortal
            id="search-bar"
            value={value}
            options={getAnimals()}
            sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } }}
            freeSolo
            blurOnSelect
            getOptionLabel={(option) => (option as Animal).commonName ?? option}
            renderInput={(params) => <TextField {...params} label="Search" />}
            onChange={handleOnChage}
        />
    )
}

export default SearchBar